.Overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(0, 0, 0, 0.8);
	z-index: 1200;
	animation: animateOverlay 1s linear;
}

.Modal {
	max-width: calc(100vw - 48px);
	max-height: calc(100vh - 24px);
	animation: animateModal 1s linear;
}
.Overlay.CloseAnimate {
	animation: animateOverlayClose 1.5s linear;
}
.Modal.CloseAnimateModal {
	animation: animateModalClose 1.5s linear;
}

@keyframes animateOverlay {
	0% {
		opacity: 0.1;
	}
	100% {
		opacity: 1;
	}
}
@keyframes animateModal {
	0% {
		transform: scale(0.95);
	}
	100% {
		transform: scale(1);
	}
}
@keyframes animateOverlayClose {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0.1;
	}
}
@keyframes animateModalClose {
	0% {
		transform: scale(1);
	}
	100% {
		transform: scale(0.9);
	}
}
